@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
.Footer__container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--primaryColor);
}

.Footer__logo {
  display: flex;
  align-items: center;
  margin-left: 20px;
  height: 100%;
}

.Footer__logoIcon {
  height: 35px;
  width: 35px;
  background-image: url(/makeShift-FE/static/media/logo_white.403e0b11.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 5px;
}

.Footer__logoText {
  width: 100%;
  color: white;
  font-family: var(--primaryFont);
  font-size: 1.2em;
}

.Footer__copyright {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  margin-right: 20px;
}

.Footer__copyIcon {
  color: white;
  margin-right: 5px;
}

.Footer__copyText {
  color: white;
}

.price {
  visibility: hidden;
}

.ui.search {
  width: 80%;
}

div.ui.icon.input {
  width: 100%;
}

/* blocks */
.navbar__mainMenu {
  width: 100%;
  padding: 5px;
  display: flex;
  justify-content: space-between;
}

.navbar__logoLink {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
}

.navbar__logoLink:hover {
  text-decoration: none;
  color: var(--primaryColor);
}

.navbar__logoLink.active {
  text-decoration: none;
}

.navbar__logoLink.active:hover {
  text-decoration: none;
}

.navbar__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
  height: 100%;
}

.navbar__searchBar {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar__linksWrapper {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 30%;
}

/* elements */
.navbar__logoText {
  font-family: var(--primaryFont);
  font-size: 2em;
  color: var(--primaryColor);
}

.navbar__logoIcon {
  height: 50px;
  width: 50px;
  margin-top: 0.5em;
  margin-right: 0.4em;
  background-image: url(/makeShift-FE/static/media/logo.f95f4ef3.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.navbar__link {
  color: black;
  font-family: var(--primaryFont);
  font-size: 1.25em;
}

.navbar__link:hover {
  color: var(--primaryColor);
  cursor: pointer;
}

.active {
  color: var(--primaryColor);
  text-decoration: underline;
}

.active:hover {
  text-decoration: underline;
}

.Login__modal {
  text-decoration: none !important;
}

.Login__header {
  color: var(--primaryColor) !important;
  font-family: var(--primaryFont) !important;
  text-align: center;
  font-size: 26px !important;
}

.Login__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid var(--primaryColor);
  padding: 20px;
  border-radius: 10px;
}

.Login__input {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px 0 2px 0;
}

.Login__form .Login__input label {
  color: black;
  font-weight: bolder;
  font-size: 20px;
  text-decoration: none;
  width: 65%;
  text-align: right;
}

.Login__form .Login__input input {
  border-style: none;
  border: 2px solid lightgrey;
  font-size: 20px;
  width: 100%;
  margin-left: 10px;
  padding: 2px;
}

.Login__form .Login__input input:focus {
  outline: none;
}

.Login__button {
  height: 50px;
  width: 20%;
  border: 2px solid var(--primaryColor);
  border-radius: 5px;
  margin-top: 20px;
  background-color: lightgrey;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-family: var(--primaryFont);
}

.Login__button:hover {
  cursor: pointer;
  box-shadow: 0 0 7px var(--primaryColor);
}

.Login__register {
  text-align: center;
  margin-top: 20px;
  font-size: 16px;
  color: black;
}

.Login__register a {
  color: var(--primaryColor);
}

/* Layout */
.hero__container {
  height: 325px;
  width: 100%;
  background-image: url(/makeShift-FE/static/media/hero.3d45c224.png);
  background-size: cover;
  background-position: center;
}

/* Block */
.hero__description {
  display: flex;
  flex-direction: column;
  width: 40%;
  margin: 90px 0 0 70px;
  opacity: 100%;
}

/* Element */
.hero__descriptionHead {
  width: 100%;
  font-size: 3em;
  color: white;
  text-shadow: 2px 2px 0 var(--primaryColor);
}

.hero__descriptionSubHead {
  width: 100%;
  font-size: 1.5em;
  font-weight: lighter;
  color: white;
  text-shadow: 0 0 10px black;
}

.PC__card {
  height: 400px;
  width: 220px;
  border-radius: 25px;
  box-shadow: 0 0 4px black;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}

.PC__linkWrapper {
  height: 55%;
  width: 100%;
}

.PC__image {
  height: 100%;
  width: 100%;
  border-radius: 25px 25px 0 0;
  background-position: center;
  background-size: cover;
  border-bottom: 0.5px solid lightgrey;
}

.PC__detailContainer {
  height: 40%;
  width: 100%;
  padding: 10px 5px 2px 5px;
}

.PC__name {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 18px;
  color: var(--primaryColor);
  font-weight: bold;
}

.PC__name:hover {
  color: var(--primaryColor);
  text-decoration: none;
}

.PC__category {
  width: 100%;
  font-size: 12px;
  color: black;
  border-bottom: 1px solid lightgrey;
  padding-bottom: 5px;
}

.PC__description {
  height: 100%;
  overflow: hidden;
}

.PC__ratingsContainer {
  width: 90%;
  padding-top: 5px;
  border-top: 1px solid lightgrey;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.PC__ratings {
  color: var(--primaryColor);
}

.Detail__container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.Detail__imgContainer {
  height: 100%;
  width: 40%;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.Detail__mainImg {
  width: 400px;
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 10px;
}

.Detail__subImgContainer {
  width: 20%;
  display: flex;
  flex-direction: column;
}

.Detail__subImg {
  width: 50px;
  height: 50px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 5px 5px 0 0;
}

.Detail__subImg.activeImg {
  box-shadow: 0 0 10px var(--primaryColor);
}

.Detail__descContainer {
  width: 50%;
  height: 400px;
  padding: 20px;
}

.Detail__name {
  color: var(--primaryColor);
  margin: 0 0 5px 0;
}

.Detail__ratingsContainer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
}

.Detail__ratingsCount {
  margin-left: 10px;
}

.Detail__descHeader {
  color: var(--primaryColor);
  text-decoration: underline;
  margin: 10px 0 3px 0;
}

#Filter__button {
  color: white;
  background: none;
  margin: 5px 5px 5px 10px;
  border: 0.5px solid white;
}

#Filter__button .text {
  width: 125px;
}

.activeFilter {
  color: var(--primaryColor);
}

#Sorter__button {
  color: white;
  background: none;
  border: 0.5px solid white;
}

#Sorter__button .text {
  width: 125px;
}

.Home__popularWrapper {
  margin: 20px auto 0 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.Home__popularHead {
  width: 40%;
  text-align: center;
  color: var(--primaryColor);
  font-size: 2em;
}

.Home__popularCards {
  width: 85%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  justify-content: space-evenly;
}

.Error__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.Error__heading {
  color: var(--primaryColor);
  margin: 3px;
  font-size: 3em;
}

.Error__gif {
  width: 325px;
  height: 325px;
  background-image: url(/makeShift-FE/static/media/facepalm.1f0760e5.gif);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 25px;
  box-shadow: 0 0 10px var(--primaryColor);
}

.Error__homeLink {
  color: var(--primaryColor);
  font-size: 1.5em;
  margin: 5px;
}

.Error__homeLink:hover {
  color: var(--primaryColor);
  text-decoration: underline;
}

.Error__description {
  margin: 5px;
}

.Catalog__container {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Catalog__controls {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100%;
  background-color: var(--primaryColor);
  box-shadow: 0 2px 3px grey;
  display: flex;
  align-items: center;
}

.Catalog__productContainer {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Register__container {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  align-items: flex-start;
  margin-bottom: 20px;
}

.Register__header {
  margin-top: 20px 0 0 0 !important;
  color: var(--primaryColor);
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
}

.Register__text {
  margin-right: 7px;
  font-family: var(--primaryFont);
}

.Register__benefits {
  width: 40%;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.Register__benefitsHead {
  width: 100%;
  border-bottom: 2px solid lightgrey;
  font-size: 22px;
  font-family: var(--primaryFont);
  color: var(--primaryColor);
}

.Register__benefits p {
  font-size: 16px;
}

.Register__form {
  width: 40%;
  margin-top: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid var(--primaryColor);
  border-radius: 15px;
}

.Register__input {
  width: 60%;
  margin: 10px 0 10px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Register__input label {
  font-size: 20px;
}

.Register__input input {
  margin-top: 10px;
  padding: 5px 2px 5px 2px;
  border-style: none;
  font-size: 18px;
  border: 1px solid lightgrey;
}

.Register__input input:focus {
  outline: none;
}

.Register__confirm {
  display: flex;
  align-items: center;
}

.Register__confirm label {
  margin-right: 5px;
}

.Register__button {
  width: 30%;
  height: 50px;
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--primaryColor);
  border-radius: 5px;
  font-size: 20px;
  font-family: var(--primaryFont);
  background: lightgrey;
}

.Register__button:hover {
  cursor: pointer;
  box-shadow: 0 0 7px var(--primaryColor);
}

:root {
  --primaryColor: #1a6762;
  --primaryFont: "Orbitron", sans-serif;
}

/* base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

*::-webkit-scrollbar {
  display: none;
}
html {
  font-size: 14px;
}

/* layout */

/* This style rule is to keep the footer at bottom of page if 
content is shorter than height of screen */
.app-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* block */
.app__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 10%;
}

.app__footer {
  width: 100%;
  height: 10%;
  margin-top: auto;
}

